<template>
    <v-dialog width="350px" persistent v-model="editDialog">
        <v-btn fab class="error" slot="activator">
            <v-icon>edit</v-icon>
        </v-btn>
        <v-card>
            <v-container>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-title>EDIT GOAL</v-card-title>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-text>
                            <v-text-field name="title" label="Title" id="title" v-model="editedTitle" required></v-text-field>
                            <v-textarea name="description" label="Description" id="description" v-model="editedDescription" required></v-textarea>
                        </v-card-text>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-actions>
                            <v-btn class="accent" light round @click="editDialog = false">Cancel</v-btn>
                            <v-btn class="success" light round @click="onSaveEdit">Save</v-btn>
                        </v-card-actions>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-card>
    </v-dialog>
</template>
<script>
    export default {
        props: ['goal'],
        data () {
            return {
                editDialog: false,
                editedTitle: this.goal.title,
                editedDescription: this.goal.description
            }
        },
        methods: {
            onSaveEdit () {
                if (this.editedTitle.trim() === '' || this.editedDescription.trim() === '') {
                    return
                }
               this.editDialog = false
               this.$store.dispatch('editGoaldata', {
                   id: this.goal.id,
                   title: this.editedTitle,
                   description: this.editedDescription 
               }) 
            }
        }
    }
</script>



